#app {
    width: 25%;
    background-color: #000000; /* Ensuring sidebar is black */
    color: white;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
}
#app h1 {
    margin-top: 0;
    text-align: center;
}
body {
    font-family: 'Arial', sans-serif;
    color: white;
    background-color: #ffffff;
    display: flex;
    margin: 0;
    padding: 0;
    height: 100vh;
}
#addProjectBtn {
    background: linear-gradient(45deg, #6a11cb 0%, #2575fc 100%);
    border: none;
    border-radius: 50px;
    color: white;
    padding: 15px 30px;
    font-size: 18px;
    font-weight: bold;
    text-transform: uppercase;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.projects-list {
    width: 100%;
    overflow-y: auto;
    flex-grow: 1;
}

.project {
    padding: 15px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #2c3e50;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-bottom: 15px;
    text-align: center;
}

.project h2 {
    margin: 0 0 10px;
    font-size: 1.2em;
}

.project p {
    margin: 0;
    font-size: 0.9em;
}

.project:hover {
    background-color: #3d566e;
}

/* Main content styles */
.main-content {
    margin-right: 75%;
    justify-content: center;
    color: black;
    width: 75%;
    padding: 20px;
    box-sizing: border-box;
}

form {
    display: flex;
    size: 100;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 20px;
}
.hidden {
    display: none;
}
form input, form select, form button {
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

form button {
    background-color: #3498db;
    color: #fff;
    cursor: pointer;
}

form button:hover {
    background-color: #2980b9;
}

#todosContainer {
    text-align: center;
    color: #333;
    margin-top: 0;
}

.todos {
    text-align: center;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
}

.todos .todo {
    padding: 15px;
    border-radius: 4px;
    background-color: #ecf0f1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border: 1px solid #bdc3c7;
}

.todos .todo.low-priority {
    background-color: #a5d6a7;
}

.todos .todo.medium-priority {
    background-color: #ffcc80;
}

.todos .todo.high-priority {
    background-color: #ef9a9a;
}

.todos .todo p {
    margin: 0 0 10px;
}

.todos .todo button {
    background-color: #e74c3c;
    color: #fff;
    padding: 5px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    align-self: flex-end;
}

.todos .todo button:hover {
    background-color: #c0392b;
}
